import { Form } from './common/Form';
import { ensureAllElements } from '../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._buttons = ensureAllElements('.button_alt', container);
        this._buttons.forEach((button) => {
            button.addEventListener('click', () => {
                if (container.querySelector('.button_alt-active')) {
                    this.toggleClass(container.querySelector('.button_alt-active'), 'button_alt-active');
                }
                button.classList.add('button_alt-active');
                this.payment = button.name;
            });
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    set payment(value) {
        this.events.emit('payment:change', { payment: value });
    }
}
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value =
            value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value =
            value;
    }
}
