import { Model } from './base/Model';
const orderDefault = {
    payment: '',
    address: '',
    email: '',
    phone: '',
    total: 0,
    items: [],
};
export class CatalogItem extends Model {
    constructor() {
        super(...arguments);
        this.inBasket = false;
    }
}
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = orderDefault;
        this.formErrors = {};
    }
    setCatalog(items) {
        this.catalog = items.map((item) => new CatalogItem(item, this.events));
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    toggleBasketArticle(id, status) {
        if (status) {
            this.order.items.push(id);
        }
        else {
            const index = this.order.items.indexOf(id);
            if (index !== -1) {
                this.order.items.splice(index, 1);
            }
        }
    }
    getBasketArticles() {
        return this.catalog.filter((item) => item.inBasket === true);
    }
    clearBasket() {
        this.catalog.forEach((article) => {
            article.inBasket = false;
        });
    }
    getTotal() {
        return this.getBasketArticles().reduce((sum, item) => sum + item.price, 0);
    }
    cleanInputs() {
        this.order.address = '';
        this.order.payment = '';
        this.order.phone = '';
        this.order.email = '';
    }
    cleanOrder() {
        this.order = orderDefault;
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (field !== 'address' && field !== 'payment') {
            if (this.validateOrder())
                this.events.emit('order:ready', this.order);
        }
        else {
            if (this.validateContacts())
                this.events.emit('order:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.email)
            errors.email = 'Необходимо указать email';
        if (!this.order.phone)
            errors.phone = 'Необходимо указать телефон';
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        if (!this.order.payment)
            errors.payment = 'Необходимо указать способ оплаты';
        if (!this.order.address)
            errors.address = 'Необходимо указать адрес';
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
