import './scss/styles.scss';
import { ShopAPI } from './components/ShopAPI';
import { API_URL, CDN_URL } from './utils/constants';
import { EventEmitter } from './components/base/Events';
import { AppState } from './components/AppData';
import { Basket } from './components/common/Basket';
import { Success } from './components/common/Success';
import { Page } from './components/Page';
import { Order, Contacts } from './components/Order';
import { CatalogArticle } from './components/Article';
import { Modal } from './components/common/Modal';
import { cloneTemplate, ensureElement } from './utils/utils';
const events = new EventEmitter();
const api = new ShopAPI(CDN_URL, API_URL);
/** Все шаблоны */
const cardCatalogTemplate = ensureElement('#card-catalog');
const cardPreviewTemplate = ensureElement('#card-preview');
const cardBasketTemplate = ensureElement('#card-basket');
const basketTemplate = ensureElement('#basket');
const orderTemplate = ensureElement('#order');
const contactsTemplate = ensureElement('#contacts');
const successTemplate = ensureElement('#success');
/** Модель данных приложения */
const appData = new AppState({}, events);
/** Глобальные контейнеры */
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
const basket = new Basket(cloneTemplate(basketTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
const contacts = new Contacts(cloneTemplate(contactsTemplate), events);
/** Вывод карточек на страницу */
events.on('items:changed', () => {
    page.gallery = appData.catalog.map((item) => {
        const card = new CatalogArticle(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item),
        });
        return card.render({
            category: item.category,
            title: item.title,
            image: item.image,
            price: item.price,
        });
    });
    page.counter = appData.getBasketArticles().length;
});
/** Изменилось состояние валидации формы */
events.on('formErrors:change', (errors) => {
    const { email, phone, address, payment } = errors;
    order.valid = !address && !payment;
    contacts.valid = !email && !phone;
    order.errors = Object.values({ address, payment })
        .filter((i) => !!i)
        .join('; ');
    contacts.errors = Object.values({ phone, email })
        .filter((i) => !!i)
        .join('; ');
});
/** Изменилось одно из полей */
events.on(/^input\..*:change/, (data) => {
    appData.setOrderField(data.field, data.value);
});
/** Изменился способ оплаты */
events.on('payment:change', (data) => {
    appData.setOrderField('payment', data.payment);
});
/** открытие окна заказа */
events.on('order:open', () => {
    appData.cleanInputs();
    appData.order.total = appData.getTotal();
    modal.render({
        content: order.render({
            address: '',
            valid: false,
            errors: [],
        }),
    });
});
/** открытие окна заполнения контактных данных */
events.on('order:submit', () => {
    modal.render({
        content: contacts.render({
            phone: '',
            email: '',
            valid: false,
            errors: [],
        }),
    });
});
/** открытие окна подтверждения покупки */
events.on('contacts:submit', () => {
    api
        .orderArticles(appData.order)
        .then((data) => {
        appData.clearBasket();
        appData.cleanOrder();
        page.counter = appData.getBasketArticles().length;
        const success = new Success(cloneTemplate(successTemplate), {
            onClick: () => {
                modal.close();
            },
        });
        modal.render({
            content: success.render({
                total: data.total,
            }),
        });
    })
        .catch((err) => {
        console.error(err);
    });
});
/** Открыть лот */
events.on('card:select', (item) => {
    appData.setPreview(item);
});
/** Изменен открытый выбранный лот */
events.on('preview:changed', (item) => {
    const card = new CatalogArticle(cloneTemplate(cardPreviewTemplate), {
        onClick: () => events.emit('card:editbasket', item),
    });
    if (item.inBasket) {
        card.toggleButton(true);
    }
    else {
        card.toggleButton(false);
    }
    modal.render({
        content: card.render({
            category: item.category,
            title: item.title,
            image: item.image,
            description: item.description,
            price: item.price,
        }),
    });
});
/** добавление или удаление товара из корзины внутри окна карточки  */
events.on('card:editbasket', (item) => {
    const card = new CatalogArticle(cloneTemplate(cardBasketTemplate), {
        onClick: () => events.emit('basket:delete', item),
    });
    const basketArticles = appData.getBasketArticles().map((item, index) => {
        return card.render({
            index: index + 1,
            title: item.title,
            price: item.price,
        });
    });
    if (!item.inBasket) {
        item.inBasket = true;
        appData.toggleBasketArticle(item.id, true);
        appData.basket = basketArticles;
    }
    else {
        item.inBasket = false;
        appData.toggleBasketArticle(item.id, false);
        appData.basket = basketArticles;
    }
    events.emit('preview:changed', item);
    page.counter = appData.getBasketArticles().length;
});
/** */
events.on('basket:delete', (item) => {
    item.inBasket = false;
    appData.toggleBasketArticle(item.id, false);
    page.counter = appData.getBasketArticles().length;
    events.emit('basket:open');
});
/** */
events.on('basket:open', () => {
    appData.basket = appData.getBasketArticles().map((item, index) => {
        const card = new CatalogArticle(cloneTemplate(cardBasketTemplate), {
            onClick: () => events.emit('basket:delete', item),
        });
        return card.render({
            index: index + 1,
            title: item.title,
            price: item.price,
        });
    });
    modal.render({
        content: basket.render({
            articles: appData.basket,
            total: appData.getTotal(),
        }),
    });
});
/** Блокируем прокрутку страницы если открыта модалка */
events.on('modal:open', () => {
    page.locked = true;
});
/** ... и разблокируем */
events.on('modal:close', () => {
    page.locked = false;
});
/**Запрос на получение карточек */
api
    .getArticleList()
    .then(appData.setCatalog.bind(appData))
    .catch((err) => {
    console.error(err);
});
